/* eslint-disable no-nested-ternary */

import { useDispatch, useSelector } from 'react-redux'
import Button from 'components/Button'
import { actions } from 'slices/app.slice'


import FavoriteIcon from '@mui/icons-material/Favorite'
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder'
import { images } from 'theme'
import HoverVideoPlayer from 'react-hover-video-player'

import { TwitchEmbed } from 'react-twitch-embed'
import Box from '@mui/material/Box'
import Rating from '@mui/material/Rating'
import Typography from '@mui/material/Typography'
import { styled } from '@mui/material/styles'
import Unity, { UnityContext } from 'react-unity-webgl'
// eslint-disable-next-line spaced-comment
//useState
import Countdown from 'react-countdown'
import React, { Suspense, useRef } from 'react'
import ReactPlayer from 'react-player/youtube'
import HackerText from 'react-hacker-text'
import {
  ContactShadows,
  Environment,
  useGLTF,
  OrbitControls,
} from '@react-three/drei'
import { Canvas, useFrame } from '@react-three/fiber'

import styles from './dashboard.module.scss'
import PausedOverlay from './PausedOverlay'

import LoadingOverlay from './LoadingOverlay'

import './styles.css'
import gltf from '../../gltf.gltf'

function Model() {
  const ref = useRef()
  // const snap = useSnapshot(state)
  const { scene } = useGLTF(gltf)
  scene.position.set(0, -4, 0)
  scene.scale.set(6, 6, 6)

  useFrame((state) => {
    const t = state.clock.getElapsedTime() * 1.4

    ref.current.rotation.y = Math.cos(t / 2) + 9
  })

  return (
    <group ref={ref} dispose={null}>
      <primitive object={scene} />
    </group>
  )
}

const StyledRating = styled(Rating)({
  '& .MuiRating-iconFilled': {
    color: '#ff6d75',
  },
  '& .MuiRating-iconHover': {
    color: '#ff3d47',
  },
})

// const cpu="4";
